import React, { Component } from 'react';
import { Button, ButtonArea, Form, FormCell, CellHeader, CellBody, CellFooter, Label, Input, VCode } from 'react-weui';
import 'weui';
import 'react-weui/build/packages/react-weui.css';
import './index.css';
import Header from '../../Layout/Header';
import vcodeSrc from '../../logo.svg';

class Login extends Component {
  componentWillMount() {
    this.setState({
      fullYear: new Date().getFullYear()
    })
  }
  render() {
    return (
      <div className="App">
        <Header appTitle="" />
        <p className="App-intro">
           <code></code> 
        </p>
        <Form>
          <FormCell>
            <CellHeader>
              <Label></Label>
            </CellHeader>
            <CellBody>
              <Input type="text" placeholder="/" />
            </CellBody>
          </FormCell>
          <FormCell>
            <CellHeader>
              <Label></Label>
            </CellHeader>
            <CellBody>
              <Input type="password" placeholder="" />
            </CellBody>
          </FormCell>
          <FormCell vcode>
            <CellHeader>
              <Label></Label>
            </CellHeader>
            <CellBody>
              <Input type="text" placeholder="" />
            </CellBody>
            <CellFooter>
              <VCode src={vcodeSrc} />
            </CellFooter>
          </FormCell>
        </Form>
        <ButtonArea direction="horizontal">
          <Button type="warn"></Button>
          <Button type="primary"></Button>
        </ButtonArea>
        <p className="loginFooter">
          APP
        </p>
        <p className="App-footer">
           <code></code> 
          <br />
          &copy; 2012-{this.state.fullYear} DingStudio Technology
        </p>
      </div>
    );
  }
}

export default Login;



