import React, { Component } from 'react';
import Header from '../../Layout/Header';
import busIcon from './images/bus.png';
import './index.css';
import { HttpClient } from '../../../Api/httpClient'
import SearchResult from "./searchResult";
import wx from 'weui.js';

class BusQueryForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            keyword: '',
            stationList: []
        };
    }
    componentDidUpdate(prevProps, prevState) {
    }
    handleChange = (element) => {
        this.setState({
            keyword: element.target.value
        })
    }
    clearSearchResult() {
        this.setState({
            stationList: []
        })
    }
    searchByKeyword() {
        if (this.state.keyword === '') {
            wx.alert('');
        } else {
            console.log(this.state.keyword)
            console.log(HttpClient);
            HttpClient.post('https://api.dscitech.com/api/bus/station/search', {
                keyword: this.state.keyword
            }).then((response) => {
                response.json().then((res) => {
                    console.log(res);
                    this.setState({
                        stationList: res.data
                    });
                }).catch((err) => {
                    console.log(err);
                    wx.alert('');
                })
            }).catch((error) => {
                console.log(error);
                wx.alert('');
            });
        }
    }
    render() {
        if (this.state.stationList.length === 0) {
            return (
                <div>
                    <Header appTitle=""/>
                    <div className="page__hd">
                        <img className="weui-media-box__thumb" src={busIcon} alt={'BusIcon'}/>
                        <div className="page__title"></div>
                        <div className="page__desc"></div>
                    </div>
                    <div className="page__bd" style={{textAlign: 'center'}}>
                        <div className="weui-cells">
                            <div className="weui-cell weui-cell_input">
                                <div className="weui-cell__hd">
                                    <div className="weui-label"></div>
                                </div>
                                <div className="weui-cell__bd">
                                    <input className="weui-input" type="text" placeholder=""
                                           value={this.state.keyword} onChange={this.handleChange.bind(this)}/>
                                </div>
                            </div>
                        </div>
                        <button className="weui-btn weui-btn_primary weui-btn_mini" onClick={this.searchByKeyword.bind(this)}>
                        </button>
                        <button className="weui-btn weui-btn_default weui-btn_mini" onClick={() => {top.location.href="https://qr.dingplace.com/scanCode.html?from=city_h5webapp"}}></button>
                        <ul style={{fontSize: '10px', padding: '10px', textAlign: 'left'}}>
                            <li></li>
                            <li>* </li>
                            <li>* GPS</li>
                            <li>* </li>
                            <li>* </li>
                            <li>* GPS</li>
                        </ul>
                        <div style={{fontSize: '10px', textAlign: 'center'}}>  
                            
                        </div>
                    </div>
                </div>
            );
        } else {
            return (
                <div>
                    <Header appTitle="" />
                    <div className="page__hd">
                        <img className="weui-media-box__thumb" src={busIcon} alt={'BusIcon'}/>
                        <div className="page__title"></div>
                        <div className="page__desc"></div>
                    </div>
                    <div className="page__bd" style={{textAlign: 'center'}}>
                        <div className="weui-panel weui-panel_access">
                            <div className="weui-panel__hd" onClick={this.clearSearchResult.bind(this)}>
                                
                            </div>
                            <div className="weui-panel__bd" style={{textAlign: 'left'}}>
                                <SearchResult stationList={this.state.stationList} />
                            </div>
                        </div>
                    </div>
                </div>
            );
        }
    }
}

export default BusQueryForm;



